
.bouclesOreilles ul {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
}

.bouclesOreilles ul li {
  border: solid black;
  border-radius: 1em;
  margin: 1em;
  width: 30em;
}

.bouclesOreilles ul li img {
  width: 90%;
  border: solid black;
  border-radius: 1em;
  margin: auto;
}
.bouclesOreilles ul {
  list-style: none;
}
@media (max-width: 1024px) {
  .bouclesOreilles .item {
    width: 90vw;
  }
}