
.bagues ul {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
}

.bagues ul li {
  border: solid black;
  border-radius: 1em;
  margin: 1em;
  width: 30em;
}

.bagues ul li img {
  width: 90%;
  border: solid black;
  border-radius: 1em;
  margin: auto;
}
.bagues ul {
  list-style: none;
}
@media (max-width: 1024px) {
  .bagues .item {
    width: 90vw;
  }
}