.contain {
    width: 100%;
  
  }
  .contBtn {
    display: flex;
    flex-direction: row;
    font-size: 0.8em;
    margin: 1em;
  }
  .contBtn .active {
    border: solid black;
  }
  
  .onglets {
    width: 50%;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 1em;
    margin: 1em;
    background: #555555;
    color: #f1f1f1;
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
    border: solid black;
    border-radius: 1em;
    box-shadow: -5px -3px 7px #333;
  }
  .bracelets .onglets{
    width: 20%;
  }

  .container {
    width: auto;
    text-align: center;
    margin: 1em;
  }
  .cont {
    margin-top: 1em;
  }
  .cont img {
    width: 100%;
  }
  
  .onglets.active {
    background: rgb(248, 248, 248);
    color: #333;
    box-shadow: 5px 3px 7px;
  }
  