
.contBtnBjx {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 0.8em;
  padding: 0.5em;
}
.contBtn .active {
  border: solid black;
}
.MenuBjx {
  padding: 1em;
  background: #555555;
  background-position-x: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #f1f1f1;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
  border: solid black;
  border-radius: 1em;
  margin: 1em;
  box-shadow: -5px -3px 7px #333;
  width: 20%;
  height: auto;
}

.buttonBjx {
  color: white;
  background: linear-gradient(#e66465, #481111);
  border-radius: 5px;
  padding: 0.5em;
}
.MenuBjx h1 {
  margin: auto;
  margin-bottom: 0;
  font-size: 1em;
}

.containerBjx {
  width: auto;
  text-align: center;
  border: solid black;
  border-radius: 1em;
}
.contBjx {
  margin-top: 0.7em;
}
/*Comportement quand active*/
.MenuBjx.active {
  background: rgb(248, 248, 248);
  color: #333;
  box-shadow: 5px 3px 7px;
  background-position-x: center;
  background-size: contain;
}
.MenuBjx.active .buttonBjx {
  background: #f1f1f1;
  color: #333;
  opacity: 0.8;
}
.MenuBjx.active h1 {
  margin: auto;
  font-size: 1.8em;
}

